@import '../../assets/commonStyle/commonStyle';

.container {
    margin: 0 auto;
    padding: 40px 50px 96px 67px;
    max-width: 1400px;
    background-color: $whiteColor;
    // min-height: calc(100vh - 139px);

}

.menuContainer {
    @include flex(row, space-between, center);
    margin-bottom: 32px;
}

.section {
    padding: 250px 0;
    @include flex(column, center, center);
    & > div:first-child {
        font-size: xx-large;
    }
}